<template>
  <div :class="{login: isLogin, 'no-login': !isLogin}">
    <template v-if="!isLogin">
      <h1>Let's share</h1>
      <p>精品博客汇聚</p>
      <div class="btns">
        <router-link to="/login">
          <el-button>立即登录</el-button>
        </router-link>
        <router-link to="/register">
          <el-button>注册账号</el-button>
        </router-link>
      </div>
    </template>
    <template v-if="isLogin">
      <h1>
        <router-link to="/">Let's share</router-link>
      </h1>
      <!--      <img class="avatar" src="http://cn.gravatar.com/avatar/1?s=128&d=identicon" alt="">-->
      <router-link to="/create">
        <i class="edit el-icon-plus"></i>
      </router-link>
      <div class="user">
        <img class="avatar" :src="user.avatar" :alt="user.username" :title="user.username">
        <ul>
          <li>
            <router-link to="/my">我的</router-link>
          </li>
          <li><a href="#" @click="onLogout">注销</a></li>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
import auth from '../api/auth'

window.auth = auth

import {mapGetters, mapActions} from 'vuex'

export default {
  data() {
    return {}
  },
  computed: {
    ...mapGetters([
      'isLogin',
      'user',
    ])
  },
  created() {
    this.checkLogin()
  },
  methods: {
    ...mapActions([
      'checkLogin',
      'logout'
    ]),
    onLogout() {
      this.logout()
      localStorage.removeItem('token')
    }
  }
}
</script>

<!-- Add ['"scoped" attribute to limit CSS to this component only -->
<!--<style scoped lang="less">-->
<style scoped>
@import "../assets/base.css";

a {
  text-decoration: none;
}

.no-login {
  padding: 0 12% 30px 12%;
  display: grid;
  justify-items: center;
  /*padding: 40px 0;*/
}

h1 {
  font-size: 40px;
  margin: 60px 0 0 0;
  text-transform: uppercase;
}
h1 a{
  color: #fff;
}

p {
  margin: 15px 0 0 0;
  color: #fff;
}

.btns {
  margin-top: 20px;
}

button {
  margin: 20px 5px 0;
}


.login {
  display: flex;
  align-items: center;
  padding: 0 40px;
}

h1 {
  margin: 0;
  color: #fff;
  font-size: 40px;
  text-transform: uppercase;
  flex: 1;
}

.edit {
  color: #fff;
  font-size: 30px;
}

.user {
   position: relative;
  margin-left: 10px;
 }
.avatar {
  width: 40px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50%;
}



.user ul {
  display: none;
  position: absolute;
  right: 0;
  list-style: none;
  border: 1px solid #eaeaea;
  padding: 0;
  background-color: #fff;
  width: 46px;
}

.user ul a {
  text-decoration: none;
  color: #333;
  font-size: 12px;
  display: block;
  padding: 5px 10px;
}

.user ul li a:hover {
  background-color: #eaeaea;
}


.user:hover ul {
  display: block;
}


</style>



