<template>
  <div id="app">
    <Header id="header"></Header>
    <Main id="main">
      <router-view/>
    </Main>
    <Footer id="footer"></Footer>
  </div>
</template>

<script>
import Header from "./components/Header";
import Footer from "./components/Footer";

export default {
  name: 'App',
  components: {
    Header,
    Footer
  }
}
</script>

<!--<style lang="less">-->
<style>
@import "assets/common.css";

html,body,#app{
  height: 100%;
}
#app{
  display: grid;
  grid-template-columns: 12% auto 12%;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:  "header header header"
                        ".      main    ."
                        "footer footer footer";
}
#header{
  grid-area: header;
  background: #149739;
}
#main{
  grid-area: main;
  /*height: 600px;*/
}
#footer{
  grid-area: footer;
  background: #ddd;
}

@media (max-width: 768px) {
  #app{
    grid-template-columns: 10px auto 10px;
  }

  #header #footer{
    padding-left: 10px;
    padding-right: 10px;
  }
}
</style>



